.Layout {
    width: 10vw;
    text-align: center;
}

.Layout-logo {
    height: 15vmin;
    pointer-events: none;
}

.Layout-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.icons,
.links {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-size: 15px;
}

.icon-link,
.app-link {
    margin: 5px 5px;
    color: white;
}