.Home {
    text-align: center;
}

.Home-logo {
    height: 25vmin;
    pointer-events: none;
}

.Home-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.icons,
.links {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-size: 25px;
}

.icon-link,
.app-link {
    margin: 10px 15px;
    color: white;
}